@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --primary: #ec7200;
  --primary-dark: #a5560d;
  --primary-light: #ff8615;
  --light: #f5f5f5;
  --light-gray: #e1e1e1;
  --gray: #888888;
  --dark-gray: #272727;
  --dark: #141414;
}

* {
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

html, body {
    font-family: 'Poppins', sans-serif;
    background-color: #272727;
    background-color: var(--dark-gray);
}

h1, h2, h3, h4, h5, p {
    margin: 0;
    padding: 0;
}

h2 {
    margin-bottom: 2%;
    font-size: 30px;
}

a {
    text-decoration: none;
    cursor: pointer;
    color: #f5f5f5;
    color: var(--light);
    padding: 2%;
    transition: 0.2s ease;
}

a:hover {
    color: #ff8615;
    color: var(--primary-light);
    text-shadow: 0 0 10px #ec7200,
    0 0 20px #ec7200,
    0 0 40px #ec7200,
    0 0 80px #ec7200,
    0 0 160px #ec7200;    
}

.active {
    color: #ff8615;
    color: var(--primary-light);
    text-shadow: 0 0 10px #ec7200,
    0 0 20px #ec7200,
    0 0 40px #ec7200,
    0 0 80px #ec7200,
    0 0 160px #ec7200;    
}

.error {
    color: #ff8615;
    color: var(--primary-light);
    font-size: 10px;
    display: block;
    height: 18px;
}

.line {
    height: 2px;
    width: 100px;
    background-color: #ec7200;
    background-color: var(--primary);
    margin-bottom: 5%;
}

.container {
    width: 100%;
    min-height: 100vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    box-sizing: border-box;
    padding: 5% 8% 8% 33%;
    background-color: #272727;
    background-color: var(--dark-gray);
    color: #e1e1e1;
    color: var(--light-gray);
    font-weight: 200;
    letter-spacing: 0.5px;
    animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

.button{
    border: none;
    background-color: #ec7200;
    background-color: var(--primary);
    color: #f5f5f5;
    color: var(--light);
    font-size: 14px;
    font-weight: 800;
    letter-spacing: 0.6px;
    padding: 15px 30px;
    cursor: pointer;
    transition: 0.4s ease all;
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.34);
}

.button:hover{
    text-decoration: none;
    background-color: #ff8615;
    background-color: var(--primary-light);
    box-shadow: 7px 7px 10px 0px rgba(0, 0, 0, 0.34);
    transform: scale(1.05);
}
.navbar{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 25%;
    top: 0;
    left: 0;
    padding: 2% 2%;
    box-sizing: border-box;
    position: fixed;
    z-index: 999999;
    background-color: var(--dark);
    box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.3);
}

.navbar header {
    color: var(--light);
    text-align: center;
}

.navbar header img {
    width: 100px;
    border: 2px solid var(--primary);
    border-radius: 50%;
}

.navbar header h1 {
    margin: 3% 0 0;
    font-weight: 500;
    font-size: 28px;
}

.navbar header h3 {
    margin: 0 0 6%;
    font-weight: 300;
    font-size: 16px;
    letter-spacing: 0.5px;
}

.navbar header p {
    margin: 0;
    font-weight: 100;
    font-size: 14px;
    letter-spacing: 0.5px;
}

.navbar .media {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-end;
    text-align: center;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10%;
    box-sizing: border-box;
}

.navbar .media i {
    font-size: 25px;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 0 2px;
}

.navbar .navlinks{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.home-body{
    background-color: var(--dark);
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 999998;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    top: 0;
    left: 0;
    padding-left: 25%;
    animation: fadeIn 0.3s ease-in-out;
}

.home-body h2{
    position: relative;
    font-size: 6em;
    letter-spacing: 10px;
    text-transform: uppercase;
    width: 100%;
    text-align: center;
    -webkit-box-reflect: below 1px linear-gradient(transparent, #0004);
    line-height: 0.7em;
    outline: none;
    animation: animate 5s linear infinite;
}

@keyframes animate {
    0%, 18%, 20%, 50.1%, 60%, 65.1%, 80%, 90.1%, 92% {
        color:#572c059c;
        text-shadow: none;
    }
    18.1%, 20.1%, 30%, 50%, 60.1%, 65%, 80.1%, 90%, 92.1%, 100% {
        color: #fff;
        text-shadow: 0 0 10px #ec7200,
                     0 0 20px #ec7200,
                     0 0 40px #ec7200,
                     0 0 80px #ec7200,
                     0 0 160px #ec7200;    
    }
}

